import { BrowserRouter, Routes, Route } from 'react-router-dom';
import './App.css';

// Layout หลัก
import T1_Layout from '../src/Components/Layout'; // T1

// หน้าเพจต่างๆ
import T2_Dashboard from '../src/Components/DashboardPage'; // T2
import T3_SalesOrder from '../src/Components/SalesOrderPage'; // T3
import QuotationSupplierListPage from './Components/pages/QuotationSupplier/QuotationSupplierListPage'; 
import QuotationSupplierPage from './Components/pages/QuotationSupplier/QuotationSupplierForm';
import QuotationAdjustListPage from './Components/pages/QuotationAdjust/QuotationAdjustListPage'; 
import QuotationAdjustPage from './Components/pages/QuotationAdjust/QuotationAdjustForm';

function App() {
  return (
    // 1. ต้องหุ้มด้วย BrowserRouter
    // 2. เพิ่ม basename={process.env.PUBLIC_URL} เพื่อให้ใช้บน GitHub Pages ได้ (แก้ปัญหาหน้าขาว)
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Routes>
        {/* T1 (Layout) เป็นตัวหุ้มหลัก */}
        <Route path="/" element={<T1_Layout />}>
          
          {/* path="index" คือหน้าแรกสุดที่จะแสดง (Dashboard) */}
          <Route index element={<T2_Dashboard />} /> 
          
          {/* Path ไม่ต้องมี / ข้างหน้า */}
          <Route path="sales-order" element={<T3_SalesOrder />} />

          <Route 
            path="quotation-supplier" 
            element={<QuotationSupplierListPage />} 
          />
          <Route 
            path="quotation-supplier/new" // ใช้ path แบบนี้จะสื่อความหมายดีกว่า
            element={<QuotationSupplierPage />} 
          />
          
          <Route 
            path="quotation-adjust" 
            element={<QuotationAdjustListPage />} 
          />
          <Route 
            path="quotation-adjust/new" // เปลี่ยนจาก quotation-adjustNew
            element={<QuotationAdjustPage />} 
          />
          
          {/* เพิ่มหน้าอื่นๆ ที่นี่ */}
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;